html{
  font-family: sans-serif;
}
.header{
  width:100%;
  /*background-color: red;*/
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin: 16px 0;
}
.logo{
  -webkit-border: 1px solid red;
  -moz-border: 1px solid red;
  border: 1px solid red;
  border-radius: 50%;
  background-color: red;
  text-align: center;
  width: 56px;
  height: 56px;
  line-height: 69px;
}
.logo img{
  width: 70%;
}
.search-form{
  width: 60%;
  margin: 0 32px;
}
label{
  position: relative;
}
.search-input{
  width: 100%;
  height: 40px;
  border-radius: 8px;
  background-color: #efefef;
  border: none;
  padding-left: 32px;
  font-size: 1.2em;
  color: #6b6b6b;
}
.search-input::-webkit-input-placeholder{
  color: #848484;
}
.search-input::-ms-input-placeholder{
  color: #848484;
}
.search-input::placeholder{
  color: #848484;
}
label:before{
  background: url(/nazmariam/kottanterest/static/media/search.8d5ace9b.svg) center no-repeat;
  content: '';
  position: absolute;
  top: 0;
  left: 8px;
  display: block;
  width: 20px;
  height: 16px;
  opacity: 0.5;
}
.submit-button{
  width: 20%;
}
.sorting-buttons{
  margin: 0 32px;
}
#decrease,
#increase{
  color: #fff;
  background-color: #8080807a;
  border: 2px dotted #ffffff;
  border-radius: 50%;
  font-size: 2em;
  margin: 0 8px;
  width: 40px;
  height: 40px;
  line-height: 32px;
}
#decrease:hover,
#increase:hover{
  background-color: rgba(128, 128, 128, 0.72);
}


@-webkit-keyframes rt {
  from {
    top:-20px
  }
  to {
    top: 0
  }
}

@keyframes rt {
  from {
    top:-20px
  }
  to {
    top: 0
  }
}

.wrapper{
  -webkit-column-count:4;
          column-count:4;
  -webkit-column-width: 200px;
          column-width: 200px;
}
.card{
  margin-bottom: 16px;
  position: relative !important;
}
.user-link{
  position: absolute;
  bottom: 16px;
  left: 16px;
  background-color: #3c3d3e;
  padding: 8px;
  border-radius: 16px;
  text-decoration: none;
  color: #fff;
  opacity: 0;
}
.rate{
  position: absolute;
  color: #fff;
  background-color: red;
  border-radius: 50%;
  top:0;
  right:16px;
  font-weight: bolder;
  width: 32px;
  height: 32px;
  text-align: center;
  line-height: 32px;
  opacity: 0;
}
.unsplash-link{
  color: #cecece;
  text-decoration: none;
  position: absolute;
  bottom: 16px;
  right: 16px;
  padding: 8px;
  font-size: 0.9em;
  opacity: 0;
}
.card:hover > .user-link{
  opacity: 1;
  transition: opacity .15s ease-in-out;
}
.card:hover > .rate{
  opacity: 1;
  transition: opacity .15s ease-in-out;
}
.card:hover > .unsplash-link{
  opacity: 1;
  transition: opacity .15s ease-in-out;
}
.card-img-top{
  width: 100%;
  border-radius: 20px;
  display: block;
}


